
.repo-list {
  list-style: none;
  padding: 0;
}
.repo {
  width: 400px;
  max-width: 100%;
  text-align: left;
  margin: auto;
  border-top: 1px gray solid;
}
.repo:first-child {
  border-top: none;
}
.repo a, .repo p{
  margin: 4px 0;
}
.repo .repo-commit {
  margin-left: 8px;
  font-size: 14px;
  color: gray
}




